<template>
  <div class="e-contact-info">
    <div class="e-contact-info__list">
      <div
        class="e-contact-info__item"
        v-for="item in contractsList"
        :key="item.name"
      >
        <div class="e-contact-info__textual">
          <div
            class="e-contact-info__circle"
            :class="item.iconFull !== undefined ? 'e-contact-info__circle_full-width' : ''"
          >
            <div
              class="e-contact-info__icon"
              :class="[
                item.iconFill ? 'e-contact-info__icon_' + item.iconFill : '',
                item.iconWidth ? 'e-contact-info__icon_' + item.iconWidth : '',
              ]"
            >
              <e-icon :src="item.icon"/>
            </div>
          </div>
          <div class="e-contact-info__inner">
            <h2 class="e-contact-info__title">{{item.title}}</h2>
            <div class="e-contact-info__descr" v-html="item.descr"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import EIcon from '@/components/EIcon';
import EButton from '@/components/EButton';
import ECircleIcon from '@/components/ECircleIcon';

export default {
  name: 'e-contact-info',

  props: {
    contractsList: {
      type: Array,
      default: () => [],
    },
  },

  components: {
    EIcon,
    ECircleIcon,
    EButton,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-contact-info {
  $this: &;

  position: relative;

  &__item {
    display: flex;
    justify-content: space-between;

    &:not(:last-of-type) {
      margin-bottom: 62px;

      @include r(1200) {
        margin-bottom: 53px;
      }
    }
  }

  &__item_affiliates {
    #{$this}__icon {
      .e-icon svg {
        fill: $color-blue;
      }
    }
  }

  &__textual {
    display: flex;
  }

  &__inner {
    padding-left: 32px;
    padding-right: 20px;
    color: $color-dark-blue;

    @include r(1200) {
      padding-left: 22px;
    }
  }

  &__circle {
    background-color: $color-white;
    border-radius: 50%;
    box-shadow: $box-shadow;
    min-height: 60px;
    min-width: 60px;
    height: 60px;
    width: 60px;
    position: relative;

    @include r(1200) {
      min-height: 50px;
      min-width: 50px;
      height: 50px;
      width: 50px;
    }
  }

  &__circle_full-width {
    background-color: transparent;

    #{$this}__icon {
      height: 100%;
      width: 100%;
    }
  }

  &__icon {
    position: absolute;
    top: 50%;
    left: 50%;
    min-height: 26px;
    min-width: 26px;
    transform: translate(-50%, -50%);

    .e-icon svg {
      fill: $color-green;
    }

    @include r(1200) {
      min-height: 20px;
      min-width: 20px;
    }
  }

  &__icon_blue {
    .e-icon svg {
      fill: $color-blue;
    }
  }

  &__title {
    font-size: 20px;
    font-weight: 600;
    margin: 0 0 5px;

    @include r(1200) {
      font-size: 16px;
      margin-bottom: -2px;
    }
  }

  &__descr {
    font-size: 16px;
    font-weight: 400;

    @include r(1200) {
      font-size: 14px;
    }
  }

  &__descr-link {
    color: $color-blue;
    text-decoration: none;
  }

  &__btn {
    min-width: 170px;
    position: relative;
    top: 15px;

    @include r(1200) {
      min-width: 145px;
      top: 10px;
    }
  }

  &__link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 60px;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
  }
}
</style>



