<template>
  <div class="the-update-ticket">
    <div class="the-update-ticket__head">
      <div class="the-update-ticket__title">Update your ticket</div>
      <div class="the-update-ticket__descr">Add details to your question, or reply </div>
      <div class="the-update-ticket__circle">
        <div class="the-update-ticket__icon">
          <e-icon :src="iconUpdate"></e-icon>
        </div>
      </div>
    </div>
    <div class="the-update-ticket__bottom">
      <div class="the-update-ticket__form">
        <div class="the-update-ticket__form-textarea the-update-ticket__form-group">
          <div class="the-update-ticket__textarea">
            <e-text-area-validated
              v-model="question"
              :vuelidate-property="$v.question"
              class="e-text-area_space"
              :icon="questionIcon"
              label="Your question"
            />
          </div>
          <div class="the-update-ticket__hint-area">
            <e-hint
              :mod="true"
              >
              A verified trader with at least $100 trading volume and you both will get <b>30 days of trading at 1/2 price*.</b>
            </e-hint>
          </div>
        </div>
        <div class="the-update-ticket__form-group the-update-ticket__form-upload">
          <div class="the-update-ticket__upload-file">
            <e-upload-files
              :max-files="5"
            />
          </div>
          <div class="the-update-ticket__upload-hint">
            <e-hint
              :mod="true"
              >
              A verified trader with at least $100 trading volume and you both will get <b>30 days of trading at 1/2 price*.</b>
            </e-hint>
          </div>
        </div>
        <div class="the-update-ticket__form-btn">
          <e-button
            :disabled="!isReadyToSubmit"
            :icon="iconArrowRight"
            :iconPosition="isReadyToSubmit ? 'right' : ''"
            type="squared"
            fill="green"
            iconMargin="none"
            width="full"
          >
            Submit
          </e-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { validationMixin } from 'vuelidate';
import { required, minLength, maxLength } from 'vuelidate/lib/validators';

import iconArrowRight from '@/icons/e-icon_arrow_right.svg';
import questionIcon from '@/icons/e-icon_chatbubble-working.svg';
import iconUpdate from '@/icons/e-icon_rounded_arrow.svg';

import ETextAreaValidated from '@/components/ETextAreaValidated';
import EUploadFiles from '@/components/EUploadFiles';
import EButton from '@/components/EButton';
import EHint from '@/components/EHint';
import EIcon from '@/components/EIcon';

export default {
  name: 'the-update-ticket',

  data() {
    return {
      iconArrowRight,
      questionIcon,
      iconUpdate,
      question: '',
      // areFilesUploading: false,
    };
  },

  computed: {
    isReadyToSubmit() {
      return !this.$v.$invalid;
    },
  },

  validations: {
    question: {
      required,
      minLength: minLength(5),
      maxLength: maxLength(1023),
    },
  },

  components: {
    ETextAreaValidated,
    EUploadFiles,
    EButton,
    EHint,
    EIcon,
  },

  mixins: [validationMixin],
};

</script>

<style lang="scss">
@import '~@/scss/styles';

.the-update-ticket {
  &__circle {
    height: 104px;
    width: 104px;
    border-radius: 50%;
    position: absolute;
    top: -6px;
    left: 0;
    background-color: $color-yellow;

    @include r(1200) {
      height: 93px;
      left: -8px;
      width: 93px;
    }
  }

  &__icon {
    height: 22px;
    width: 27px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -14px;
    margin-left: -13px;

    .e-icon svg {
      fill: rgb(9, 22, 138);
    }
  }

  &__title {
    font-size: 60px;
    color: $color-dark-blue;
    font-weight: 400;
    line-height: 1;
    margin-bottom: 8px;

    @include r(1200) {
      font-size: 40px;
      padding-top: 14px;
    }
  }

  &__descr {
    font-size: 16px;
    color: $color-dark-blue;
    font-weight: 400;
    padding-left: 3px;

    @include r(1200) {
      font-size: 14px;
    }
  }

  &__head {
    padding-left: 30px;
    position: relative;
    padding-left: 133px;
    margin-left: 37px;
    margin-bottom: 35px;

    @include r(1200) {
      margin-left: 0;
      padding-left: 105px;
      margin-bottom: 25px;
    }
  }

  &__form {
    max-width: 651px;
    padding-left: 170px;

    @include r(1200) {
      padding-left: 106px;
      max-width: 380px;
    }
  }

  &__form-textarea {
    margin-left: -48px;
    margin-right: -50px
  }

  &__form-group {
    margin-bottom: 20px;
    display: flex;
  }

  &__form-upload {
    margin-right: -54px;
  }

  &__textarea {
    width: 100%;
    margin-right: 18px;
  }

  &__hint-area {
    margin-top: 15px;
  }

  &__upload-file {
    width: 100%;
  }

  &__upload-hint {
    display: flex;
    align-items: center;
    margin-left: 24px;
  }
}
</style>



