<template>
  <div
    class="e-text-input"
    :class="componentClass"
  >
    <div
      class="e-text-input__left-outer-icon-wrap"
      v-if="leftOuterIcon"
    >
      <div
        class="e-text-input__left-outer-icon"
        :class="`e-text-input__left-outer-icon_${leftOuterIconSize}`"
      >
        <e-icon :src="leftOuterIcon"/>
      </div>
    </div>
    <div class="e-text-input__box">
      <div
        v-if="leftInnerIcon"
        class="e-text-input__icon"
      >
        <e-icon :src="leftInnerIcon"/>
      </div>
      <div
        class="e-text-input__label"
        v-text="label"
      />
      <input
        v-model="textValue"
        v-bind="$attrs"
        class="e-text-input__input"
        @focus="focusHandler"
        @blur="blurHandler"
      />
      <div
        v-if="rightInnerIcon"
        class="e-text-input__icon e-text-input__icon_right"
      >
        <e-icon :src="rightInnerIcon"></e-icon>
      </div>
    </div>
  </div>
</template>

<script>
import EIcon from '@/components/EIcon';

export default {
  name: 'e-text-input',
  inheritAttrs: false,

  props: {
    value: {
      type: [Number, String],
      default: '',
    },

    label: String,
    iconRelative: Boolean,

    width: {
      type: String,
      default: 'm',
    },

    leftOuterIcon: String,
    leftOuterIconSize: {
      type: String,
      default: 'm',
    },
    leftInnerIcon: String,
    rightInnerIcon: String,
  },

  data() {
    return {
      textValue: (this.value != null && `${this.value}`) || '',
      isFocused: false,
    };
  },

  computed: {
    componentClass() {
      return {
        focused: this.isFocused,
        [`e-text-input_size-${this.width}`]: true,
        'has-text': this.hasText,
        'right-inner-icon-present': this.rightInnerIcon,
        'left-inner-icon-present': this.leftInnerIcon,
      };
    },
    hasText() {
      return this.textValue.length > 0;
    },
  },

  watch: {
    value(value) {
      this.textValue = value;
    },
    textValue(value) {
      this.$emit('input', value);
    },
  },

  methods: {
    focusHandler() {
      this.isFocused = true;
      this.$emit('focus', this.textValue);
    },

    blurHandler() {
      this.isFocused = false;
      this.$emit('blur', this.textValue);
    },
  },

  components: { EIcon },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-text-input {
  display: flex;
  font-size: 16px;
  fill: #dadada;

  &_size-full &__box {
    width: 100%;
  }

  &_size-m &__box {
    width: 475px;
    @include r(1200) {
      width: 425px;
    }
  }

  &_size-l &__box {
    width: 500px;
  }

  &_size-xl &__box {
    width: 600px;
    @include r(1200) {
      width: 439px;
    }
  }

  &__left-outer-icon_m {
    width: 23px;
  }

  &__left-outer-icon_semi {
    width: 25px;
  }

  &__left-outer-icon_xl {
    width: 44px;
    min-width: 44px;
    max-width: 44px;
  }

  &__left-outer-icon-wrap {
    min-width: 35px;
    width: 35px;
    max-width: 35px;
    height: 60px;
    margin: auto 15px auto 0;
    display: flex;
    align-items: center;
    justify-content: center;
    backface-visibility: hidden;

    & .e-icon svg {
      top: 50%;
      transform: translateY(-50%);
      transition: $transition-time;
    }

    @include r(1200) {
      height: 50px;
    }
  }

  &__label {
    position: absolute;
    top: 17px;
    left: 4px;
    font-size: 16px;
    color: black;
    margin: 0 7px 0;
    transform: scale(1) translate(0);
    transform-origin: top left;
    transition: $transition-time;
    pointer-events: none;

    @include r(1200) {
      font-size: 14px;
      top: 12px;
    }
    .focused &, .has-text & {
      transform: scale(.75) translate(2px, -9px);
    }
  }

  &__box {
    display: flex;
    border: 2px solid #ebecef;
    background: white;
    box-sizing: border-box;
    box-shadow: 0 7px 10px rgba(0, 0, 0, 0.03);
    border-radius: 5px;
    height: 60px;
    overflow: hidden;
    position: relative;
    transition: $transition-time;

    @include r(1200) {
      height: 50px;
    }
  }

  &__input {
    color: #0d105b;
    flex: 1;
    padding: 16px 14px 0;
    vertical-align: top;

    &::-webkit-contacts-auto-fill-button {
      opacity: 0;
      visibility: hidden;
    }
    &::-webkit-credentials-auto-fill-button {
      opacity: 0;
      visibility: hidden;
    }

    @include r(1200) {
      font-size: 14px;
    }
  }

  &.right-inner-icon-present &__input {
    padding: 16px (2 * 14px + 20px) 0 14px;
  }

  &.left-inner-icon-present &__input {
    padding: 16px 14px 0 (2 * 14px + 20px);
  }

  &__icon {
    width: 20px;
    height: 100%;
    position: absolute;
    margin: 0 14px;
    display: flex;
    align-items: center;
    justify-content: center;

    & .e-icon svg {
      top: 50%;
      transform: translateY(-50%);
    }
  }

  &__icon_right {
    right: 0;
  }

  &__icon_left {
    left: 0;
  }
}
</style>



