<template>
  <div
    class="e-upload-files"
    :class="[
      rootClass,
      display ? `e-upload-files__display-${display}`: 'e-upload-files__display-default',
      fill ? `e-upload-files__fill-${fill}`: 'e-upload-files__fill-default',
    ]"
  >
    <label
      class="e-upload-files__upload"
      tabindex="0"
      v-show="!limitReached"
    >
      <div class="e-upload-files__in">
        <input
          class="e-upload-files__input"
          type="file"
          v-bind="{ accept, multiple, ...$attrs }"
          @change="processFile($event)"
        >
        <div class="e-upload-files__icon">
          <e-icon :src="iconUpload" />
        </div>
        <div class="e-upload-files__title">
          {{ computedLabel }}
        </div>
        <div
          class="e-upload-files__textual"
          v-if="multiple"
        >
            Max. {{ maxFiles }} files ({{ acceptLabel }})
        </div>
      </div>
    </label>
    <div class="e-upload-files__files">
      <div
        class="e-upload-files__file"
        v-for="{ key, percentage, file: { size, name } } of files"
        :key="key"
      >
        <span
          class="e-upload-files__file-delete"
          @click="removeFile(key)"
        >
          <e-icon :src="iconDelete"/>
        </span>
        <span class="e-upload-files__file-label">
          <span class="e-upload-files__file-name">
            {{ name }}
          </span>
          <span class="e-upload-files__file-size">
            {{ size | bytesToSize }}
          </span>
        </span>
        <div
          class="e-upload-files__file-progress"
          :style="uploadPercentage | uploadPercentageToStyle"
        />
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable no-param-reassign */
import EIcon from '@/components/EIcon';

import iconUpload from '@/icons/e-icon_upload.svg';
import iconDelete from '@/icons/e-icon_close.svg';

import log from '@/util/logger';
import { bytesToSize } from '@/util/filters';

let lastFileKey = 0;

const uploadPercentageToStyle = p => ({ width: p === 100 ? '0' : `${p}%` });

export default {
  name: 'e-upload-files',

  model: {
    event: 'change',
  },

  props: {
    value: [File, Array],
    uploadPercentage: [Number, Array],

    maxFiles: {
      type: Number,
      default: 1,
    },

    accept: {
      type: String,
      default: 'image/png,image/jpeg',
    },

    label: String,
    display: String,
    fill: String,

    acceptLabel: {
      type: String,
      default: '.PNG, .JPG',
    },

    smallDesign: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      files: [],
      iconUpload,
      iconDelete,
    };
  },

  computed: {
    rootClass() {
      return {
        'no-accept-description': this.smallDesign === true,
        'has-file-uploaded': this.files.length > 0,
      };
    },

    computedLabel() {
      if (this.label != null) {
        return this.label;
      }
      return `Upload file${this.multiple ? 's' : ''}`;
    },

    multiple() {
      return this.maxFiles > 1;
    },

    limitReached() {
      return this.files.length >= this.maxFiles;
    },
  },

  watch: {
    value(value) {
      if (value === null) {
        this.files = [];
      } else if (value instanceof File) {
        const prevFileDescriptor = this.files[0];
        if (!prevFileDescriptor || prevFileDescriptor.file !== value) {
          lastFileKey += 1;
          this.files = [{
            file: value,
            key: lastFileKey,
            percentage: this.uploadPercentage,
          }];
        }
      } else if (Array.isArray(value)) {
        log.error(`array value is not supported in ${this.$options.name}`);
        throw new Error('Not supported');
      }
    },

    files: {
      handler: 'updateUploadPercentage',
      deep: true,
    },

    uploadPercentage: {
      handler: 'updateUploadPercentage',
      deep: true,
    },
  },

  methods: {
    processFile(event) {
      const inputFiles = event.target.files;
      if (inputFiles.length + this.files.length > this.maxFiles) {
        // TODO notify about overflow
        event.target.value = null;
        return;
      }
      for (let i = 0; i < inputFiles.length; i += 1) {
        lastFileKey += 1;
        this.files.push({
          file: inputFiles[i],
          key: lastFileKey,
          percentage: this.uploadPercentage,
        });
        this.emitChange();
      }
      event.target.value = null;
    },

    removeFile(removedKey) {
      const removedIndex = this.files.findIndex(({ key }) => key === removedKey);
      this.files.splice(removedIndex, 1);
      this.emitChange();
    },

    emitChange() {
      if (this.multiple) {
        this.$emit('change', this.files.map(f => f.file));
      } else {
        this.$emit('change', (this.files[0] && this.files[0].file) || null);
      }
    },

    updateUploadPercentage() {
      const percentage = this.uploadPercentage;
      if (!isNaN(percentage) && this.files[0]) {
        this.files[0].percentage = percentage;
      } else if (Array.isArray(percentage) && percentage.length) {
        // TODO
      }
    },
  },

  components: { EIcon },

  filters: {
    bytesToSize,
    uploadPercentageToStyle,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-upload-files {
  $this: &;

  background-color: $color-white;

  &__upload {
    border-radius: 63px;
    color: $color-blue;
    cursor: pointer;
    font-weight: bold;
    position: relative;
    box-sizing: border-box;
    user-select: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 67px;
    padding-right: 21px;
    height: 50px;
    outline: 0;
    transition: $transition-time;
    box-shadow: inset 0 1px 5px rgba(#09168A, 0);

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      border-radius: 63px;
      box-shadow: 0 3px 10px rgba(#94A5B4, .14);
      transition: $transition-time;
      opacity: 1;
    }

    &:hover {
      background-color: $color-blue;
      box-shadow: inset 0 1px 5px rgba(#09168A, 1);

      &:before {
        opacity: 0;
      }

      #{$this}__textual {
        color: rgba(white, .5);
      }

      #{$this}__title {
        color: #fff;
      }

      #{$this}__icon svg {
        fill: #fff;
      }
    }
  }

  &.has-file-uploaded &__upload {
    margin-bottom: 10px;
  }

  &__input {
    display: none;
  }

  &__in {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &.no-accept-description {
    background-color: transparent;

    #{$this}__in {
      align-items: flex-start;
      justify-content: center;
      flex-direction: column;
    }
  }

  &__title {
    text-transform: uppercase;
    font-size: 11px;
    letter-spacing: .85px;
    padding-top: 2px;
    transition: $transition-time;

    @include r(1200) {
      font-size: 10px;
      padding-top: 1px;
    }
  }

  &__textual {
    color: $color-dark-blue;
    font-size: 14px;
    font-weight: 400;
    text-align: right;
    transition: $transition-time;
    opacity: 1;

    @include r(1200) {
      font-size: 12px;
    }
  }

  &__icon {
    width: 22px;
    height: 15px;
    left: 29px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);

    svg {
      fill: $color-blue;
      width: 23px;
      transition: $transition-time;

      @include r(1200) {
        width: 22px;
      }
    }
  }

  &__file {
    margin: 10px 0;
    background: #FFFFFF;
    box-shadow: 0 3px 10px rgba(148, 165, 180, 0.14);
    border-radius: 63.5px;
    height: 40px;
    padding: 0 21px 0 24px;
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: $transition-time;

    &:hover {
      box-shadow: inset 0 1px 5px rgba(#09168A, 1);
      background-color: $color-blue;

      #{$this}__file-size {
        color: rgba(white, .5);
      }
    }
  }

  &__file-delete {
    position: relative;
    fill: $color-red;
    min-width: 14px;
    cursor: pointer;

    & .e-icon svg {
      top: 50%;
      transform: translateY(-50%);
    }
  }

  &__file-label {
    flex: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
  }

  &__file:first-child {
    margin-top: 0;
  }

  &__file:last-child {
    margin-bottom: 0;
  }

  &__file-name {
    flex: 1;
    padding: 0 16px;
    font-weight: bold;
    font-size: 11px;
    letter-spacing: 0.846154px;
    text-transform: uppercase;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    color: #1FCE43;
  }

  &__file-size {
    font-weight: 400;
    font-size: 14px;
    color: #0D105B;
    transition: $transition-time;
  }

  &__file-progress {
    border-radius: 0 0 63px 63px;
    height: 2px;
    background: $color-green-bright;
    position: absolute;
    bottom: 0;
    left: 0;
  }

  &__display-square {
    #{$this}__upload {
      padding: 0 20px;
      border-radius: 5px;
      height: 57px;
    }

    #{$this}__file {
      padding: 0 20px;
      border-radius: 5px;
      height: 57px;
    }

    #{$this}__icon {
      left: auto;
      right: 20px;
      width: 15px;
      height: 10px;

      svg {
        width: 15px;
        height: 10px;
        fill: #00E997;
      }
    }
  }

  &__fill-transparent-white {
    background-color: transparent;

    #{$this}__upload {
      box-shadow: none;
      border: 2px solid rgba(31, 238, 188, 0.4);

      &::before {
        box-shadow: none;
      }

      &:hover {
        background-color: #fff;
        border-color: transparent;

        #{$this}__title {
          color: #010D5F;
        }

        #{$this}__icon {
          svg {
            fill: #010D5F;
          }
        }
      }
    }

    #{$this}__files {
      box-shadow: none;

      &:hover {
        background-color: transparent;
      }
    }

    #{$this}__file {
      border: 2px solid rgba(31, 238, 188, 0.4);
      background-color: transparent;
      box-shadow: none;
      color: #fff;
    }
    #{$this}__file-size {
      color: #fff;
    }

    #{$this}__title {
      color: #fff;
      font-size: 16px;
      font-weight: 500;
      text-transform: none;
      padding-top: 0;
    }

    #{$this}__icon {
      svg {
        fill: #00E997;
      }
    }
  }
}
</style>



