<template>
  <div class="e-no-activity" @click="blockVisibility = 1">
    <div class="e-no-activity__icon">
      <e-icon :src="icon" />
    </div>
    <div class="e-no-activity__textual">
      <div class="e-no-activity__sticker">{{ title }}</div>
      <div class="e-no-activity__text" v-html="text"></div>
    </div>
  </div>
</template>

<script>
import EIcon from '@/components/EIcon';

import iconSpider from '@/icons/e-icon_spider.svg';

export default {
  name: 'e-no-activity',

  data() {
    return {
      iconSpider,
    };
  },

  props: {
    title: {
      type: String,
      default: '',
    },
    text: {
      type: String,
      default: '',
    },
    icon: {
      type: String,
      default: iconSpider,
    },
  },

  components: {
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-no-activity {
  display: flex;

  &__icon {
    margin-right: 52px;
    width: 106px;
    margin-top: 11px;

    @include r(1200) {
      margin-right: 32px;
      margin-top: 0;
      width: 86px;
    }
  }

  &__sticker {
    background-color: $color-yellow;
    border-radius: 17px;
    display: inline-block;
    padding: 5px 20px 4px;
    font-size: 16px;
    letter-spacing: .1px;
    margin-bottom: 23px;

    @include r(1200) {
      margin-bottom: 14px;
      font-size: 14px;
      padding: 7px 20px 6px;
    }
  }

  &__textual {
    color: $color-dark-blue;
    font-weight: 400;
    max-width: 330px;

    @include r(1200) {
      font-size: 14px;
      max-width: 280px;
    }
  }

  &__text {
    font-size: 16px;
    line-height: 1.32;
    letter-spacing: .1px;

    @include r(1200) {
      line-height: 1.5;
      font-size: 14px;
    }
  }
}
</style>



