<template>
  <e-text-area
    v-model="actualValue"
    :label="label"
    :left-outer-icon="icon"
    class="e-text-area-validated"
    :class="textAreaClass"
    @blur="handleBlur"
    @focus="handleFocus"
  />
</template>

<script>
import ETextArea from '@/components/ETextArea';

export default {
  name: 'e-text-area-validated',

  props: {
    label: String,
    icon: String,

    value: {
      type: String,
      required: false,
    },

    vuelidateProperty: {
      type: Object,
      required: true,
    },
  },

  data() {
    return {
      actualValue: this.value,
      isFocused: false,
    };
  },

  computed: {
    isEmpty() {
      return this.actualValue.length === 0;
    },

    isValid() {
      return !this.vuelidateProperty.$invalid;
    },

    isError() {
      return this.vuelidateProperty.$error;
    },

    textAreaClass() {
      if (this.isFocused) {
        return '';
      }
      return {
        valid: this.isValid,
        error: this.isError,
      };
    },
  },

  watch: {
    actualValue(val) {
      this.vuelidateProperty.$touch();
      this.$emit('input', val);
    },
  },

  methods: {
    handleBlur(val) {
      this.isFocused = false;
      this.$emit('blur', val);
    },

    handleFocus(val) {
      this.isFocused = true;
      this.$emit('focus', val);
    },
  },

  components: { ETextArea },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-text-area-validated {

  // valid: dirty + non-focused + valid

  &.valid .e-text-area__box {
    border-color: #40d046;
  }

  &.valid .e-text-area__icon, &.valid .e-text-area__left-outer-icon {
    fill: #40d046;
  }

  // error: dirty + non-focused + invalid

  &.error .e-text-area__box {
    border-color: #d34637;
  }

  &.error .e-text-area__icon, &.error .e-text-area__left-outer-icon {
    fill: #d34637;
  }
}
</style>



