<template>
  <div class="the-tickets">
    <div class="the-tickets__head">
      <div class="the-tickets__col the-tickets__col_wide">
        <div class="the-tickets__heading">Created</div>
      </div>
      <div class="the-tickets__col the-tickets__col_wide">
        <div class="the-tickets__heading">Last activity</div>
      </div>
      <div class="the-tickets__col the-tickets__col_wide">
        <div class="the-tickets__heading">Subject</div>
      </div>
      <div class="the-tickets__col the-tickets__col_space">
        <div class="the-tickets__heading">Last message</div>
      </div>
      <div class="the-tickets__col the-tickets__col_last">
      </div>
    </div>
    <div class="the-tickets__body">
      <div class="the-tickets__row" v-for="item in tickets" :key="item.subject">
        <div class="the-tickets__col the-tickets__col_wide">
          <div class="the-tickets__title">{{item.createdDate}}</div>
          <div class="the-tickets__subtitle">{{item.creadtedTime}}</div>
        </div>
        <div class="the-tickets__col the-tickets__col_wide">
          <div class="the-tickets__title">{{item.activityDate}}</div>
          <div class="the-tickets__subtitle">{{item.activityTime}}</div>
        </div>
        <div class="the-tickets__col the-tickets__col_wide">
          <div class="the-tickets__title">{{item.subject}}</div>
        </div>
        <div class="the-tickets__col the-tickets__col_space">
          <div class="the-tickets__text">{{item.lastMessage}}</div>
        </div>
        <div class="the-tickets__col the-tickets__col_last">
          <div class="the-tickets__status"
            :class="[
            item.solved ? 'the-tickets__status_check' : '',
            item.newMessage ? 'the-tickets__status_alert' : ''
            ]">
            <template v-if="item.newMessage">
              New message
            </template>
            <template v-if="item.solved">
              Solved
            </template>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'the-tickets',

  data() {
    return {
      tickets: [{
        createdDate: '05 Feb 2018',
        creadtedTime: '20:00',
        activityDate: '16 Feb 2018',
        activityTime: '13:00',
        subject: 'Money issue',
        lastMessage: 'Hello, my name is Borat, I need to buy some analghin and public three names.',
        solved: false,
        newMessage: true,
      }, {
        createdDate: '04 Feb 2018',
        creadtedTime: '20:00',
        activityDate: '15 Feb 2018',
        activityTime: '12:15',
        subject: 'General questions',
        lastMessage: 'If you look at what you have in life, you’ll always have more. If you look at what you don’t have in life...',
        solved: false,
        newMessage: true,
      }, {
        createdDate: '01 Feb 2018',
        creadtedTime: '18:15',
        activityDate: '10 Feb 2018',
        activityTime: '12:15',
        subject: 'Hello',
        lastMessage: 'Do not go where the path may lead, go instead where there is no path and leave a trail.',
        solved: true,
      }],
    };
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-tickets {
  &__head {
    display: flex;
    padding-left: 27px;
    padding-right: 29px;
    margin-bottom: 9px;

    @include r(1200) {
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  &__col {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }

  &__col_last {
    align-items: flex-end;
    flex-basis: 15%;
  }

  &__col_wide {
    flex-basis: 15%;
    max-width: 15%;
    padding-right: 10px;
    box-sizing: border-box;

    @include r(1200) {
      padding-right: 0;
    }
  }

  &__col_space {
    flex-basis: 40%;
    box-sizing: border-box;
    padding-right: 20px;

    @include r(1200) {
      padding-left: 14px;
      padding-right: 20px;
    }
  }
  &__row {
    display: flex;
    box-shadow: 0 3px 10px rgba(#94A5B4, .14);
    background-color: #fff;
    min-height: 80px;
    padding-left: 26px;
    padding-right: 30px;

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    @include r(1200) {
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  &__title {
    font-size: 14px;
    color: black;
    font-weight: 400;
    line-height: 1;
    margin-bottom: 3px;

    @include r(1200) {
      font-size: 12px;
    }
  }

  &__subtitle {
    font-size: 14px;
    color: rgba(black, .5);
    line-height: 1;
  }

  &__text {
    font-size: 14px;
    color: black;
    font-weight: 400;

    @include r(1200) {
      font-size: 12px;
      line-height: 1.34;
    }
  }

  &__heading {
    font-size: 15px;
    color: rgba(black, .5);

    @include r(1200) {
      font-size: 13px;
    }
  }

  &__status {
    font-size: 12px;
    text-transform: uppercase;
    position: relative;
    font-weight: 400;
    letter-spacing: 1px;
    padding-left: 17px;
    white-space: nowrap;

    &:before {
      content: '';
      position: absolute;
      top: 2px;
      left: 0;
      height: 10px;
      width: 10px;
      border-radius: 50%;
    }

    @include r(1200) {
      font-size: 10px;
      padding-left: 13px;
    }
  }

  &__status_check {
    color: $color-green-bright;

    &:before {
      background-color: $color-green-bright;
    }
  }

  &__status_alert {
    color: $color-blue;

    &:before {
      background-color: $color-blue;
    }
  }
}
</style>



