<template>
  <div class="the-issue">
    <div class="the-issue__head">
      <div class="the-issue__col the-issue__col_wide">
        <div class="the-issue__heading">Created</div>
      </div>
      <div class="the-issue__col the-issue__col_wide">
        <div class="the-issue__heading">Last activity</div>
      </div>
      <div class="the-issue__col the-issue__col_wide">
        <div class="the-issue__heading">Subject</div>
      </div>
      <div class="the-issue__col the-issue__col_space">
        <div class="the-issue__heading">Last message</div>
      </div>
      <div class="the-issue__col the-issue__col_last"></div>
    </div>
    <div class="the-issue__body">
      <div class="the-issue__table">
        <div class="the-issue__row" v-for="item in ticket" :key="item.subject">
          <div class="the-issue__col the-issue__col_wide">
            <div class="the-issue__title">{{item.createdDate}}</div>
            <div class="the-issue__subtitle">{{item.creadtedTime}}</div>
          </div>
          <div class="the-issue__col the-issue__col_wide">
            <div class="the-issue__title">{{item.activityDate}}</div>
            <div class="the-issue__subtitle">{{item.activityTime}}</div>
          </div>
          <div class="the-issue__col the-issue__col_wide">
            <div class="the-issue__title">{{item.subject}}</div>
          </div>
          <div class="the-issue__col the-issue__col_space">
            <div class="the-issue__text">{{item.lastMessage}}</div>
          </div>
          <div class="the-issue__col the-issue__col_last">
            <div class="the-issue__status"
              :class="[
              item.solved ? 'the-issue__status_check' : '',
              item.newMessage ? 'the-issue__status_alert' : ''
              ]">
              <template v-if="item.newMessage">
                New message
              </template>
              <template v-if="item.solved">
                Solved
              </template>
            </div>
          </div>
        </div>
      </div>
      <div class="the-issue__dialog">
        <e-dialog></e-dialog>
      </div>
    </div>
    <div class="the-issue__add">
      <the-update-ticket/>
    </div>
  </div>
</template>
<script>
import EDialog from '@/components/EDialog';
import TheUpdateTicket from '@/containers/blocks/TheUpdateTicket';

export default {
  name: 'the-issue',

  data() {
    return {
      ticket: [{
        createdDate: '05 Feb 2018',
        creadtedTime: '20:00',
        activityDate: '16 Feb 2018',
        activityTime: '13:00',
        subject: 'Money issue',
        lastMessage: 'Hello, my name is Borat, I need to buy some analghin and public three names.',
        solved: false,
        newMessage: true,
      }],
    };
  },

  components: {
    EDialog,
    TheUpdateTicket,
  },
};

</script>

<style lang="scss">
@import '~@/scss/styles';

.the-issue {
  &__head {
    display: flex;
    padding-left: 28px;
    padding-right: 28px;
    margin-bottom: 9px;

    @include r(1200) {
      margin-left: 0;
      padding: 0 20px 4px 17px;
    }
  }

  &__col {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }

  &__col_last {
    align-items: flex-end;
    flex-basis: 15%;
  }

  &__col_wide {
    flex-basis: 15%;
    max-width: 15%;

    @include r(1200) {
      padding-right: 10px;
      box-sizing: border-box;
    }
  }

  &__col_space {
    flex-basis: 40%;
    padding-right: 10px;
    box-sizing: border-box;

    @include r(1200) {
      padding-left: 13px;
      padding-right: 20px;
    }
  }

  &__heading {
    font-size: 15px;
    color: rgba(black, .5);
  }

  &__body {
    box-shadow: 0 3px 10px rgba(#94A5B4, .14);
    padding: 0 25px 25px;
    margin-bottom: 59px;

    @include r(1200) {
      padding: 0 20px 4px 17px;
      margin-bottom: 46px;
    }
  }

  &__table {
    border-bottom: 2px solid $color-light-grey;
  }

  &__row {
    display: flex;
    background-color: #fff;
    min-height: 80px;
    padding-left: 4px;
    padding-right: 6px;

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__title {
    font-size: 14px;
    color: black;
    font-weight: 400;
    line-height: 1;
    margin-bottom: 3px;

    @include r(1200) {
      font-size: 12px;
    }
  }

  &__subtitle {
    font-size: 14px;
    color: rgba(black, .5);
    line-height: 1;
  }

  &__text {
    font-size: 14px;
    color: black;
    font-weight: 400;
    @include r(1200) {
      font-size: 12px;
    }
  }

  &__heading {
    font-size: 15px;
    color: rgba(black, .5);
    @include r(1200) {
      font-size: 13px;
    }
  }

  &__status {
    font-size: 12px;
    text-transform: uppercase;
    position: relative;
    font-weight: 400;
    letter-spacing: 1px;
    padding-left: 17px;
    white-space: nowrap;

    &:before {
      content: '';
      position: absolute;
      top: 3px;
      left: 0;
      height: 10px;
      width: 10px;
      border-radius: 50%;

      @include r(1200) {
        top: 2px;
      }
    }

    @include r(1200) {
      font-size: 10px;
      padding-left: 13px;
    }
  }

  &__status_check {
    color: $color-green-bright;

    &:before {
      background-color: $color-green-bright;
    }
  }

  &__status_alert {
    color: $color-blue;

    &:before {
      background-color: $color-blue;
    }
  }

  &__dialog {
    padding-left: 2px;
  }

  &__add {
    border-top: 2px solid $color-light-grey;
    padding-top: 50px;
    margin-bottom: 100px;

    @include r(1200) {
      border-top: none;
      padding-top: 0;
      margin-bottom: 58px;
    }
  }
}
</style>



