<template>
  <div
    class="page-support"
    :class="{ 'is-popup-showing': isPopupShowing }"
  >
    <transition
      name="transition-header"
      :duration="{ enter: 2000, leave: 600 }"
    >
      <div
        v-if="isPopupShowing"
        class="page-news__header"
      >
        <the-header-inner/>
      </div>
    </transition>

    <div class="page-support__root">
      <div id="page-about__wrapp" ref="contentWrap" class="page-post__wrapp">
        <the-header-new />

        <div class="page-support__main">

        <div class="page-support__main-wrapper">
          <div class="page-support__main-content">

            <div class="page-support__main-sidebar">
              <div class="page-support__sidebar-post">
                <router-link :to="{ name: 'aboutus' }" class="page-support__sidebar-post-title">About us</router-link>
              </div>
              <div class="page-support__sidebar-post">
                <router-link :to="{ name: 'aboutethermium' }" class="page-support__sidebar-post-title">What is DMEX?</router-link>
              </div>
              <div class="page-support__sidebar-post">
                <router-link :to="{ name: 'aboutdecenralization' }" class="page-support__sidebar-post-title">What is Decentralization?</router-link>
              </div>
              <div class="page-support__sidebar-post">
                <router-link :to="{ name: 'support' }" :class="{ 'router-link-exact-active': ['aboutus'].includes($route.name) }" class="page-support__sidebar-post-title">Contact us</router-link>
              </div>
              <div class="page-fees__sidebar-post">
                <router-link :to="{ name: 'fees' }" class="page-fees__sidebar-post-title">Fees</router-link>
              </div>
            </div>

            <div class="page-support__main-container">
              <div class="page-support__heading">
                <div @click="$router.go(-1)" class="page-support__back"></div>
                <div class="page-support__heading-main">Contact us</div>
                <div class="page-support__heading-description">We are here to answer any questions you may have about DMEX. Reach out and we will get back to you as soon as possible.</div>
              </div>

              <div class="page-support__box">
                <e-contact-info
                  :contractsList="contactList"
                />
              </div>
            </div>
          </div>
        </div>
      </div>

        <the-footer/>
      </div>
    </div>

    <!-- popup and menu -->
    <the-popup v-if="isPopupShowing"/>
    <the-menu-sidebar v-if="isPopupShowing"/>
    <!-- popup and menu -->
  </div>
</template>

<script>
/**
 * Component: Страница поста
 *
 * @version 2.0.0
 */


import { mapGetters } from 'vuex';

// Components
import iconArrow from '@/icons/e-icon_arrow.svg';
import iconBack from '@/icons/e-icon__back-big.svg';
import iconUmbrella from '@/icons/e-icon_umbrella.svg';
import iconDocument from '@/icons/e-icon_document-text.svg';
import supportIcon from '@/icons/e-icon_support.svg';
import iconCompose from '@/icons/e-icon_compose.svg';
import iconDialog from '@/icons/e-icon_dialog.svg';
import iconSpider from '@/icons/e-icon_spider.svg';
import iconTwitter from '@/icons/e-icon_twitter.svg';
import iconLetter from '@/icons/e-icon_letter.svg';
import iconTelegram from '@/icons/e-icon_telegram.svg';

import EButton from '@/components/EButton';
import EItemsTitle from '@/components/EItemsTitle';
import EIcon from '@/components/EIcon';
import TheIssue from '@/containers/blocks/TheIssue';
import TheTickets from '@/containers/blocks/TheTickets';
import EButtonBack from '@/components/EButtonBack';
import TheNewTicket from '@/containers/blocks/TheNewTicket';
import ENoActivity from '@/components/ENoActivity';
import EContactInfo from '@/components/EContactInfo';

import TheTokenSearchEtherscanMobile from '@/containers/blocks/TheTokenSearchEtherscanMobile';
import EBigNumber from '@/components/EBigNumber';
import TheFooter from '@/containers/blocks/TheFooter';
import TheHeaderInner from '@/containers/blocks/TheHeaderInner';
import TheHeaderNew from '@/containers/blocks/TheHeaderNew';
import TheMenuSidebar from '@/containers/blocks/TheMenuSidebar';
import ThePopup from '@/containers/blocks/ThePopup';
import TheMobileTokenSearch from '@/containers/blocks/TheMobileTokenSearch';
import TheTokenSearchResultNew from '@/containers/blocks/TheTokenSearchResultNew';


export default {
  name: 'page-support',

  data() {
    return {
      supportIcon,
      iconCompose,
      iconDialog,
      iconSpider,
      blockVisibility: 0,
      iconUmbrella,
      iconDocument,
      iconArrow,
      iconBack,
    };
  },

  computed: {
    ...mapGetters({
      isPopupShowing: 'ui/isPopupShowing',
    }),
    contactList() {
      return [{
        title: 'Send us a mail',
        descr: 'Official e-mail: <a class="e-contracts-box__descr-link" href="mailto:contact@dmex.app">contact@dmex.app</a>',
        icon: iconLetter,
        iconWidth: 'wide',
        url: 'mailto:contact@ethermium.com',
        btnTitle: 'E-MAIL US',
        iconFull: true,
      }, {
        title: 'Join our Telegram group',
        descr: 'Official link: <a class="e-contracts-box__descr-link" href="https://t.me/dmex_app" target="_blank">https://t.me/dmex_app</a>',
        icon: iconTelegram,
        iconWidth: 'wide',
        url: 'https://t.me/dmex_app',
        btnTitle: 'join telegram',
        iconFull: true,
      }, {
        title: 'Follow or Message us on Twitter',
        descr: 'Official link <a class="e-contracts-box__descr-link" href="https://twitter.com/DMEX_APP" target="_blank"> https://twitter.com/DMEX_APP</a>',
        icon: iconTwitter,
        url: 'https://twitter.com/DMEX_APP',
        btnTitle: 'twitter',
        iconFull: true,
      }];
    },
  },

  metaInfo: {
    title: 'Support EtherMium.com',
    meta: [
      {
        name: 'description',
        content: 'If you have any problems, please, contact us immediately.  Our team will help you as quickly as possible',
      },
    ],
  },

  async mounted() {
    this.$ga.page(this.$router);
  },

  components: {
    EButton,
    EItemsTitle,
    EIcon,
    TheIssue,
    TheTickets,
    EButtonBack,
    TheNewTicket,
    ENoActivity,
    EContactInfo,
    TheTokenSearchEtherscanMobile,
    TheMobileTokenSearch,
    TheTokenSearchResultNew,
    EBigNumber,
    TheFooter,
    TheHeaderInner,
    TheHeaderNew,
    TheMenuSidebar,
    ThePopup,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.page-support {
  $this: &;

  &__root {
    font-weight: 500;
    background-color: #f2f2f2;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    overflow-y: scroll;
  }

  &__wrapp {
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
    height: 100%;
  }

  &__header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 78px;
    z-index: 102;
  }

  // MAIN section
  // ==================================================
  &__main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #031472;
    position: relative;
    margin-top: -120px;
    padding-top: 120px;
    z-index: 0;

    &::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      z-index: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(to bottom, rgba(5,4,26,.5) 0%,rgba(5,4,26,.4) 30%, rgba(5,4,26,0) 70%, rgba(5,4,26,0) 100%);
    }

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      transform: translate(-50%, 0);
      z-index: 0;
      bottom: -120px;
      width: 100%;
      max-width: 1600px;
      height: 180px;
      background: #030C52;
      border-radius: 50%;
      filter: blur(30px);
      opacity: .4;

      @include r(992) {
        display: none;
      }
    }
  }

  &__main-wrapper {
    background: #f2f2f2;
    z-index: 1;
  }

  &__main-content {
    width: 100%;
    max-width: 1440px;
    padding: 70px 130px 190px;
    box-sizing: border-box;
    margin: 0 auto;
    position: relative;
    display: flex;
    justify-content: space-between;

    @include r(1200) {
      padding: 70px 90px 150px;
    }

    @include r(992) {
      position: relative;
      padding: 50px 40px;
      flex-direction: column-reverse;
      justify-content: flex-start;
    }
  }

  &__main-container {
    width: 75%;
    margin-right: 120px;
    position: relative;

    @include r(1220) {
      margin-right: 60px;
    }

    @include r(992) {
      position: relative;
      padding: 50px 40px;
      flex-direction: column-reverse;
      justify-content: flex-start;
    }
  }

  &__heading {
    font-weight: normal;
    margin-bottom: 32px;
    position: relative;

    @include r(640) {
      margin-bottom: 26px;
    }
  }

  &__back {
    position: absolute;
    display: inline-block;
    width: 17px;
    height: 24px;
    cursor: pointer;
    left: -47px;
    top: 16px;

    @include r(768) {
      left: -32px;
    }

    @include r(640) {
      top: 11px;
    }

    &::before, &::after {
      display: block;
      position: absolute;
      content: '';
      width: 17px;
      height: 2px;
      background: #1f2ee9;
    }

    &::before {
      transform: rotate(-45deg);
      left: 0;
      top: 5px;
    }

    &::after {
      transform: rotate(45deg);
      left: 0;
      top: 16px;
    }
  }

  &__heading-main {
    color: #0d105b;
    font-size: 34px;
    font-weight: normal;
    margin-bottom: 20px;

    @include r(640) {
      line-height: 43px;
    }
  }

  &__heading-description {
    color: #000;
    font-size: 23px;
    line-height: 36px;
  }

  &__box {
    padding-left: 85px;
    margin: 84px 0 65px;

    @include r(1200) {
      padding-left: 28px;
    }

    a {
      text-decoration: none;
    }
  }

  &__items-title {
    padding: 74px 0px 56px 18px;

    @include r(1200) {
      padding: 28px 0px 36px 5px;
    }
  }

  &__title-support {
    padding: 74px 0px 23px 18px;

    @include r(1200) {
      padding: 31px 0px 24px 4px;
    }
  }

  &__main-sidebar {
    padding-top: 19px;
    width: 25%;

    @include r(992) {
      position: static;
      transform: translateX(0);
      box-shadow: none;
      width: 100%;
      padding-top: 70px 0 0;
      background: transparent;
      height: auto;
    }
  }

  &__sidebar-post {
    margin-bottom: 16px;
  }

  &__sidebar-post-title {
    font-size: 16px;
    color: #1F2EE9;
    line-height: 21px;
    letter-spacing: 0.5px;
    transition: all 0.2s;
    cursor: pointer;
    margin-top: 5px;
    text-decoration: none;

    &.router-link-exact-active {
      color: #000;
    }

    &:hover {
      color: #000;
    }
  }
}

</style>



