<template>
  <div class="the-sent-form-answer">
      <div class="the-sent-form-answer__in">
        <div class="the-sent-form-answer__icon">
          <e-icon :src="iconPaperAirplaine" />
        </div>
        <div class="the-sent-form-answer__content">
          <div class="the-sent-form-answer__title">{{ title }}</div>
          <div class="the-sent-form-answer__text">{{ text }}</div>
          <div class="the-sent-form-answer__btns">
            <slot />
          </div>
        </div>
      </div>
  </div>
</template>

<script>
import EIcon from '@/components/EIcon';

import iconPaperAirplaine from '@/icons/e-icon_paper-airplane.svg';

export default {
  name: 'the-sent-form-answer',

  data() {
    return {
      iconPaperAirplaine,
    };
  },

  props: {
    title: {
      type: String,
      default: 'Your question was sent to our Support',
    },
    text: {
      type: String,
      default: ' Please, be patient and wait for your answer. Our Support team will get back to you as soon as it possible.',
    },
  },

  components: {
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';
.the-sent-form-answer {
  &__in {
    display: flex;
  }

  &__icon {
    background-color: $color-green-bright;
    border-radius: 50%;
    height: 99px;
    margin-right: 33px;
    min-height: 99px;
    min-width: 99px;
    width: 99px;

    .e-icon svg {
      fill: $color-white;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 20px;
    }

    @include r(1200) {
      margin-right: 20px;
      height: 80px;
      min-height: 80px;
      min-width: 80px;
      width: 80px;
    }
  }

  &__content {
    padding-top: 22px;
    max-width: 372px;
  }

  &__title {
    color: $color-green-bright;
    font-size: 20px;

    @include r(1200) {
      font-size: 16px;
    }
  }

  &__text {
    font-size: 20px;
    font-weight: 400;
    line-height: 1.4;
    margin-bottom: 37px;

    @include r(1200) {
      font-size: 16px;
    }
  }

  &__btns {
    .e-button {
      &:not(:last-of-type) {
        margin-bottom: 22px;
      }
    }
  }
}
</style>



