<template>
  <e-text-input
    v-model="actualValue"
    v-bind="{ label, width, ...$attrs }"
    class="e-text-input-validated"
    :class="textInputClass"
    :left-outer-icon="icon"
    :right-inner-icon="validationIcon"
    :left-outer-icon-size="iconSize"
    @blur="handleBlur"
    @focus="handleFocus"
  />
</template>

<script>
import validIcon from '@/icons/e-icon_checkbox-outline.svg';
import ETextInput from '@/components/ETextInput';

export default {
  name: 'e-text-input-validated',

  props: {
    value: {
      type: [Number, String],
      default: '',
    },

    width: {
      type: String,
      default: 'm',
    },

    label: String,
    iconSize: String,
    icon: String,

    // validation
    vuelidateProperty: {
      type: Object,
      required: true,
    },

    isValidationIconDisplayed: Boolean,

    recolorText: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      actualValue: this.value,
      isFocused: false,
    };
  },
  computed: {
    isEmpty() {
      return this.actualValue.length === 0;
    },

    isDirty() {
      return this.vuelidateProperty.$dirty;
    },

    isValid() {
      return !this.vuelidateProperty.$invalid;
    },

    isError() {
      return this.vuelidateProperty.$error;
    },

    textInputClass() {
      const classes = {
        'recolor-text': this.recolorText,
      };
      if (this.isFocused) {
        return {
          focused: true,
          ...classes,
        };
      }
      if (!this.isDirty) {
        return classes;
      }
      return {
        valid: this.isValid,
        error: this.isError,
        ...classes,
      };
    },

    validationIcon() {
      if (this.isValidationIconDisplayed && !this.isFocused && this.isDirty && this.isValid) {
        return validIcon;
      }
      return null;
    },
  },

  watch: {
    actualValue(val) {
      this.vuelidateProperty.$touch();
      this.$emit('input', val);
    },
  },

  methods: {
    handleBlur(val) {
      if (this.isEmpty && this.isValid) {
        this.vuelidateProperty.$reset();
      }
      this.isFocused = false;
      this.$emit('blur', val);
    },

    handleFocus(val) {
      this.isFocused = true;
      this.$emit('focus', val);
    },
  },

  components: { ETextInput },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-text-input-validated {
  $this: &;

  // no class: non-dirty non-focused
  .e-icon_wallet-adress_darked {
    fill: #878794;
  }

  // focused: focused

  &.focused .e-text-input__input {
    color: $color-blue;
  }

  &.focused .e-text-input__box {
    border-color: $color-blue;
  }

  &.focused {
    fill: #647AFD;

    .e-icon_wallet-adress_darked {
      fill: $color-blue;
    }
  }

  // valid: dirty + non-focused + valid

  &.valid.recolor-text .e-text-input__input {
    color: $color-green-bright;
  }

  &.valid .e-text-input__box {
    border-color: $color-green-bright;
  }

  &.valid {
    fill: $color-green-bright;

    .e-icon_wallet-adress_darked {
      fill: #0D8E22;
    }
  }

  // error: dirty + non-focused + invalid

  &.error.recolor-text .e-text-input__input {
    color: $color-red;
  }

  &.error .e-text-input__box {
    border-color: $color-red;
  }

  &.error {
    fill: $color-red;

    .e-icon_wallet-adress_darked {
      fill: #9B1600;
    }
  }
}
</style>



