<template>
  <div
    class="e-text-area"
    :class="classes"
  >
    <div class="e-text-area__left-outer-icon-wrap">
      <div
        v-if="leftOuterIcon"
        class="e-text-area__left-outer-icon"
      >
        <e-icon :src="leftOuterIcon"/>
      </div>
    </div>
    <div class="e-text-area__box">
      <div class="e-text-area__label">
        {{ label }}
      </div>
      <div
        v-if="leftInnerIcon"
        class="e-text-area__icon"
      >
        <e-icon :src="leftInnerIcon"/>
      </div>
      <textarea
        v-model="actualValue"
        v-bind="$attrs"
        @focus="focusHandler"
        @blur="blurHandler"
        class="e-text-area__input"
      />
    </div>
  </div>
</template>

<script>
import EIcon from '@/components/EIcon';

export default {
  name: 'e-text-area',
  inheritAttrs: false,

  props: {
    value: {
      type: String,
      default: '',
    },

    label: String,
    leftInnerIcon: String,
    leftOuterIcon: String,
  },

  data() {
    return {
      actualValue: this.value,
      isFocused: false,
    };
  },

  computed: {
    classes() {
      return {
        focused: this.isFocused,
        'has-text': this.actualValue.length,
      };
    },
  },

  watch: {
    actualValue(val) {
      this.$emit('input', val);
    },
  },

  methods: {
    focusHandler() {
      this.isFocused = true;
      this.$emit('focus', this.actualValue);
    },

    blurHandler() {
      this.isFocused = false;
      this.$emit('blur', this.actualValue);
    },
  },

  components: { EIcon },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-text-area {
  $this: &;

  display: flex;
  font-size: 16px;
  fill: #dadada;

  &_space {
    #{$this}__box {
      width: 100%;
    }
  }
  &__left-outer-icon-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 15px auto 0;
    min-width: 35px;
    width: 35px;
    max-width: 35px;
    height: 60px;

    & .e-icon svg {
      top: 50%;
      transform: translateY(-50%);
    }
  }

  &__label {
    position: absolute;
    top: 17px;
    left: 4px;
    font-size: 16px;
    color: black;
    margin: 0 7px 0;
    transform: scale(1) translate(0);
    transform-origin: top left;
    transition: .3s ease;
    pointer-events: none;

    .focused &, .has-text & {
      transform: scale(.75) translate(2px, -9px);
    }

    @include r(1200) {
      top: 12px;
    }
  }

  &__left-outer-icon {
    width: 27px;
    transition: .3s ease;
  }

  &__box {
    display: flex;
    height: 200px;
    width: 400px;
    border: 2px solid #ebecef;
    box-sizing: border-box;
    box-shadow: 0 7px 10px rgba(0, 0, 0, 0.03);
    border-radius: 5px;
    padding: 0 7px;
    background: white;
    color: #0d105b;
    position: relative;
    transition: .3s ease;

    @include r(1200) {
      height: 120px;
      width: 380px;
    }
  }

  &__input {
    padding: 23px 7px 0;
    flex: 1;
    border: 0;
    resize: none;
    box-shadow: none;
    outline: none;
    overflow: hidden;
  }

  &__icon {
    fill: rgba(13, 16, 91, 0.2);
    width: 25px;
    height: 100%;
    margin: 14px 7px 0;
  }

  .focused &__box {
    border-color: #1e00e5;
  }

  .focused &__icon, &.focused &__left-outer-icon {
    fill: #1e00e5;
  }
}
</style>



