<template>
  <div class="e-dialog">
    <div class="e-dialog__section"
      v-for="item in dialog"
      :key="item.name"
      :class="[
        item.client ? 'e-dialog__section_message' : '',
        item.support ? 'e-dialog__section_answer' : ''
      ]"
      >
      <div class="e-dialog__head">
        <div class="e-dialog__icon">S</div>
        <div class="e-dialog__in">
          <div class="e-dialog__name">{{item.name}}</div>
          <div class="e-dialog__date">{{item.date}}</div>
        </div>
      </div>
      <div class="e-dialog__text">
        {{item.text}}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'e-dialog',

  data() {
    return {
      dialog: [{
        client: true,
        name: 'Simion Muntean',
        date: 'Tue, 21 Nov, 2017 at 11:18 AM',
        text: 'Hello, my name is Borat, I need to buy some analghin and public three names. A Block Explorer allows you transparently trace and track transactions that take place on the Blockchain. And we do not have control or access to your coins / private keys or any transactions that you make.',
      }, {
        support: true,
        name: 'Support',
        date: 'Tue, 21 Nov, 2017 at 11:18 AM',
        text: 'A Block Explorer allows you transparently trace and track transactions that take place on the Blockchain. And we do not have control or access to your coins / private keys or any transactions that you make.',
      }, {
        client: true,
        name: 'Simiun Muntean',
        date: 'Tue, 21 Nov, 2017 at 11:18 AM',
        text: 'A Block Explorer allows you transparently trace and track transactions that take place on the Blockchain. And we do not have control or access to your coins / private keys or any transactions that you make.',
      }],
    };
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-dialog {
  $this: &;

  &__section {
    padding-top: 22px;
    padding-bottom: 24px;
  }

  &__section_answer {
    background-color: $color-light-grey;
    border-radius: 10px;
    padding: 17px 24px 25px;
    margin-bottom: 8px;

    #{$this}__icon {
      background-image: linear-gradient(#FF681E, #FFBF00);
    }

    #{$this}__text:before {
      background-image: linear-gradient(#FF681E, #FFBF00);
    }

    #{$this}__head {
      margin-bottom: 18px;
      @include r(1200) {
        margin-bottom: 13px;
      }
    }

    @include r(1200) {
      padding: 21px 24px 15px;
    }
  }

  &__section_message {
    #{$this}__icon {
      background-image: linear-gradient(#17DA9D, #66FFB2);
    }

    #{$this}__text:before {
      background-color: #17DA9D;
    }
  }

  &__head {
    display: flex;
    margin-bottom: 9px;

    @include r(1200) {
      margin-bottom: 12px;
    }
  }

  &__icon {
    height: 30px;
    width: 30px;
    border-radius: 50%;
    color: #fff;
    font-size: 17px;
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 1px;
    box-sizing: border-box;

    @include r(1200) {
      height: 25px;
      width: 25px;
    }
  }

  &__in {
    padding-left: 9px;
  }

  &__name {
    font-size: 17px;
    color: black;
    font-weight: 400;
    line-height: 1;
    margin-bottom: 4px;

    @include r(1200) {
      font-size: 14px;
      margin-bottom: 0;
    }
  }

  &__date {
    font-size: 10px;
    color: $color-grey;
    letter-spacing: .67px;
  }

  &__text {
    font-size: 16px;
    font-weight: 400;
    color: $color-dark-blue;
    position: relative;
    padding-left: 28px;
    margin-left: 11px;
    line-height: 1.32;

    @include r(1200) {
      padding-left: 24px;
      font-size: 14px;
      line-height: 1.5em;
    }

    &:before {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      top: 3px;
      width: 9px;
      border-radius: 5px;
      background-color: black;

      @include r(1200) {
        width: 6px;
      }
    }
  }
}
</style>



