<template>
  <div class="the-new-ticket">

    <transition-content>
      <form class="the-new-ticket__form" v-show="!formSent">
        <div class="the-new-ticket__group">
          <e-text-input-validated
            v-model="email"
            :vuelidate-property="$v.email"
            type="email"
            required
            label="Your e-mail"
            :icon="iconEmail"
            :isValidationIconDisplayed="true"
          />
          <div class="the-new-ticket__hint">
            <e-hint :mod="true">
              A verified trader with at least $100 trading volume and you both will get <b>30 days of trading at 1/2 price*.</b>
            </e-hint>
          </div>
        </div>
        <div class="the-new-ticket__group">
          <e-text-input-validated
            v-model="subject"
            :vuelidate-property="$v.subject"
            type="text"
            label="Subject"
            :icon="bugIcon"
          />
          <div class="the-new-ticket__hint">
            <e-hint :mod="true">
              A verified trader with at least $100 trading volume and you both will get <b>30 days of trading at 1/2 price*.</b>
            </e-hint>
          </div>
        </div>
        <div class="the-new-ticket__group">
          <e-text-area-validated
            v-model="troubleDescription"
            :vuelidate-property="$v.troubleDescription"
            label="Trouble with..."
            :icon="iconQuestion"
          />
          <div class="the-new-ticket__hint">
            <e-hint :mod="true">
              A verified trader with at least $100 trading volume and you both will get <b>30 days of trading at 1/2 price*.</b>
            </e-hint>
          </div>
        </div>
        <div class="the-new-ticket__group">
          <div class="the-new-ticket__in">
            <div class="the-new-ticket__file">
              <e-upload-files
                :max-files="5"
              />
            </div>
            <div class="the-new-ticket__file-hint">
              <e-hint :mod="true">
                A verified trader with at least $100 trading volume and you both will get <b>30 days of trading at 1/2 price*.</b>
              </e-hint>
            </div>
          </div>
        </div>
        <div class="the-new-ticket__group">
          <div class="the-new-ticket__in">
            <e-button
              :disabled="!isReadyToSubmit"
              :icon="iconArrowRight"
              :iconPosition="isReadyToSubmit ? 'right' : ''"
              type="squared"
              fill="green"
              iconMargin="none"
              width="full"
              @click="sendForm"
            >
              Send
            </e-button>
          </div>
        </div>
      </form>
    </transition-content>

    <transition-content>
      <div class="the-new-ticket__answer" v-show="formSent">
        <the-sent-form-answer>
          <e-button
            fill="green"
            padding="secondary"
            iconMargin="md"
            :icon="iconCompose"
            @click="handleClick(3)"
          >
            create other ticket
          </e-button>
        </the-sent-form-answer>
      </div>
    </transition-content>

  </div>
</template>

<script>
import { validationMixin } from 'vuelidate';
import { required, minLength, maxLength, email } from 'vuelidate/lib/validators';

import iconArrowRight from '@/icons/e-icon_arrow_right.svg';
import iconEmail from '@/icons/e-icon_form-letter.svg';
import bugIcon from '@/icons/e-icon_bug.svg';
import iconQuestion from '@/icons/e-icon_chatbubble-working.svg';
import iconCompose from '@/icons/e-icon_compose.svg';

import ETextInputValidated from '@/components/ETextInputValidated';
import ETextAreaValidated from '@/components/ETextAreaValidated';
import EButton from '@/components/EButton';
import ESwitch from '@/components/ESwitch';
import EUploadFiles from '@/components/EUploadFiles';
import EHint from '@/components/EHint';
import TheSentFormAnswer from '@/containers/blocks/TheSentFormAnswer';

import TransitionContent from '@/containers/transitions/TransitionContent';

export default {
  name: 'the-new-ticket',

  data() {
    return {
      iconEmail,
      bugIcon,
      iconQuestion,
      iconArrowRight,
      iconCompose,
      email: '',
      subject: '',
      troubleDescription: '',
      // areFilesUploading: false,
      formSent: false,
    };
  },

  computed: {
    isReadyToSubmit() {
      return !this.$v.$invalid;
    },
  },

  validations: {
    email: {
      required,
      email,
    },
    subject: {
      required,
      minLength: minLength(5),
    },
    troubleDescription: {
      required,
      minLength: minLength(5),
      maxLength: maxLength(1023),
    },
  },

  methods: {
    sendForm() {
      this.formSent = true;
    },

    handleClick(index) {
      this.formSent = false;
      this.$emit('handleClick', index);
    },
  },

  components: {
    ESwitch,
    EUploadFiles,
    EButton,
    ETextAreaValidated,
    ETextInputValidated,
    EHint,
    TheSentFormAnswer,
    TransitionContent,
  },

  mixins: [validationMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-new-ticket {
  &__form {
    max-width: 489px;
    margin-left: 85px;
  }

  &__group {
    margin-bottom: 20px;
    display: flex;
  }

  &__hint {
    margin-top: 15px;
    margin-left: 20px;
  }

  &__in {
    width: 100%;
    padding-left: 50px;
    padding-right: 38px;
    max-width: 400px;
    display: flex;
    position: relative;
  }

  &__file {
    width: 100%;
  }

  &__file-hint {
    position: absolute;
    top: 10px;
    right: -11px;
  }

  &__answer {
    padding-top: 45px;

    @include r(1200) {
      padding-top: 20px;
      padding-left: 26px;
    }
  }
}

</style>



